export const shareKakao = (webUrl, thumbImg) => { // url id    route  
    if (window.Kakao) {
      const kakao = window.Kakao;
      if (!kakao.isInitialized()) {
        kakao.init(process.env.REACT_APP_KAKAO_JAVASCRIPT_KEY); //   javascript key  -> .env 
      }

  kakao.Link.sendDefault({
    objectType: "feed", //     type  feed  ->    
    content: {
      title: '   !', // 
      description: "  ,    ", // 
      imageUrl: thumbImg,
      link: {
        mobileWebUrl: webUrl, // URL
        webUrl: webUrl
      }
    },
    buttons: [
      {
        title: "",
        link: {
          mobileWebUrl: webUrl,
          webUrl: webUrl
        }
      }
    ]
  });
}
};